.projectouter:hover {
  border: 3.5px solid rgba(230, 173, 137, 0.637);
  background: #2b273b;
  background-image: linear-gradient(170.4deg, blue -10.9%, orange);
  color: blue;
  transform: scale(1.05);
  transition-property: transform;
  transition-duration: 1.5s;
}
.iconbutton {
  text-decoration: none;
  border-radius: 60px;
  display: inline;
  align-items: center;
  overflow: hidden;
  width: auto;
  color: orange;
  max-width: 50px; 
  -webkit-transition: max-width 0.5s;
  transition: max-width 0.5s;
}

.iconbutton:hover {
  max-width: 700px;
}

.icon {
  font-family: "Font Awesome 5 Free";
  font-size: 16px;
  margin-right: 15px;
  padding: 0px 8px;
  display: flex;
  align-items: center;
}

.text {
  white-space: nowrap;
  padding-right: 15px;
}
