.container {
  height: 100vh;
}

.rotateyes {
  transform: rotate(180deg);
  transition-property: transform;
  transition-duration: 1s;
}

.wrapper {
  width: 100%;
  height: 700px;
  overflow: hidden;
}
.wrapper .elem:nth-child(1) {
  animation: anim 1s cubic-bezier(0.19, 1, 0.22, 1) 1s both;
}

@keyframes anim {
  to {
    margin-top: -700px;
  }
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 900;
}

.logo p {
  margin: 0;
  font-weight: 900;
}

.first {
  font-size: 28px;
  color: darkred;
  font-family: "Lobster", cursive;
}

.last {
  font-size: 22px;
  font-weight: 900;
  font-family: "Lobster", cursive;
}
.code {
  font-size: 28px;
  color: chocolate;
}
