* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
.skills-card {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-height: 85vh;
  color: orange;
  flex-direction: row;
}
ul ol {
  margin-bottom: 75px;
  font-size: 30px;
  font-weight: 900;
  transform: translate(0.1em, -0.1em);
}
ul ol:hover {
  background-color: orange;
  color: blue;
  transform: translate(0.1em, -0.1em);
  -webkit-transform: translate(0.9, -0.2em);
  border-radius: 5px 5px;
  width: 100%;
  transition: all 0.25s linear;
  box-shadow: -14px -6px 5px rgb(234, 177, 72);
}
@media (max-width: 760px) {
  .skills-card {
    flex-direction: column;
    min-height: fit-content;
  }
  ul:nth-child(1) {
    margin-top: 30px;
  }
  ul:nth-child(2) {
    margin-top: 60px;
    margin-right: 230px;
  }
  ul ol {
    margin-bottom: 65px;
  }
  ul li {
    position: relative;
    list-style: none;
    width: 200px;
    height: 55px;
  }
}
ul {
  transform: skewY(-14deg);
}
ul li {
  position: relative;
  list-style: none;
  width: 200px;
  height: 60px;
  background-color: rgb(1, 1, 84);
  padding: 15px;
  transition: 1.5s;
  z-index: var(--i);
}
ul li a {
  text-decoration: none;
  color: orange;
  display: block;
  letter-spacing: 0.05em;
  transition: 1.5s;
}

ul li::before {
  content: "";
  position: absolute;
  top: 0;
  left: -40px;
  width: 40px;
  height: 100%;
  background-color: rgb(1, 1, 84);
  filter: brightness(0.7);
  transform-origin: right;
  transform: skewY(45deg);
  transition: 1.5s;
}
ul li::after {
  content: "";
  position: absolute;
  top: -40px;
  left: 0;
  width: 102%;
  height: 45px;
  background: rgb(1, 1, 84);
  filter: brightness(0.9);
  transform-origin: bottom;
  transform: skewX(43deg);
}

ul li:hover::after {
  background-color: var(--clr);
  filter: brightness(0.9);
}
li:nth-child(1) {
  --i: 8;
}
li:nth-child(2) {
  --i: 7;
}
li:nth-child(3) {
  --i: 6;
}
li:nth-child(4) {
  --i: 5;
}
li:nth-child(5) {
  --i: 4;
}
li:nth-child(6) {
  --i: 3;
}
li:nth-child(7) {
  --i: 2;
}
li:nth-child(8) {
  --i: 1;
}
li:nth-child(1):hover {
  transform: translateX(-150px);
  background-color: #c43143;
}
li:nth-child(2):hover {
  transform: translateX(-120px);
  background-color: #25d366;
}
li:nth-child(3):hover {
  transform: translateX(-160px);
  background-color: #1da1f2;
}
li:nth-child(4):hover {
  transform: translateX(-160px);
  background-color: #c32aa3;
}
li:nth-child(5):hover {
  transform: translateX(-160px);
  background-color: #ff0000;
}
li:nth-child(6):hover {
  transform: translateX(-160px);
  background-color: #0a66c2;
}
li:nth-child(7):hover {
  transform: translateX(-150px);
  background-color: #1877f2;
}
li:nth-child(8):hover {
  transform: translateX(-160px);
  background-color: #25d366;
}
li:nth-child(1):hover::before {
  background-color: #c43143;
}
li:nth-child(2):hover::before {
  background-color: #25d366;
}
li:nth-child(3):hover::before {
  background-color: #1da1f2;
}
li:nth-child(4):hover::before {
  background-color: #c32aa3;
}
li:nth-child(5):hover::before {
  background-color: #ff0000;
}
li:nth-child(6):hover::before {
  background-color: #0a66c2;
}
li:nth-child(7):hover::before {
  background-color: #1877f2;
}
li:nth-child(8):hover::before {
  background-color: #25d366;
}
ul li:nth-child(1):hover::after {
  background-color: #c43143;
}
li:nth-child(2):hover::after {
  background-color: #25d366;
}
li:nth-child(3):hover::after {
  background-color: #1da1f2;
}
li:nth-child(4):hover::after {
  background-color: #c32aa3;
}
li:nth-child(5):hover::after {
  background-color: #ff0000;
}
li:nth-child(6):hover::after {
  background-color: #0a66c2;
}
li:nth-child(7):hover::after {
  background-color: #1877f2;
}
li:nth-child(8):hover::after {
  background-color: #25d366;
}
ul li:last-child::after {
  box-shadow: -100px 120px 10px rgb(1, 1, 84);
}

#github-stats-card {
  transition: transform 0.5s linear;
}
#github-stats-card:hover {
  background: #000;
  transform: scale(0.9);

  transition: 0.5s ease-in;
}

#github-streak-stats {
